const rules=[
    [/#{6}\s?([^\n]+)/g,'<h6>$1</h6>'],
    [/#{5}\s?([^\n]+)/g,'<h5>$1</h5>'],
    [/#{4}\s?([^\n]+)/g,'<h4>$1</h4>'],
    [/#{3}\s?([^\n]+)/g,'<h3>$1</h3>'],
    [/#{2}\s?([^\n]+)/g,'<h2>$1</h2>'],
    [/#{1}\s?([^\n]+)/g,'<h1>$1</h1>'],
    [ /\*\*([^\n\n]+)\*\*/g,'<b>$1</b>'],
    [ /\*([^\n]+)\*/g,'<i>$1</i>'],
    [/\[([^\n]+)\]\(([^\n)]+)\)/g,'<a href=\'$2\'>$1</a>'],
    [/\*{1}\s?([^\n]+)/g,'<ul><li>$1</li></ul>'],
    [/~([^\n]+)~/g,'<del>$1</del>'],
    
    
    
    
    
];

export default{
    bind(el){
        let html=el.textContent;
        rules.forEach(([rules,template])=>{
            html=html.replace(rules,template);
        })
        el.innerHTML=html;
    }
}